.popup {
	display: flex;
	flex-direction: column;
    margin: 16px;
    background: hsla(0, 0%, 30%, 0.5);
	position: fixed;
	height: 100vh;
	width: 100vw;
	transition: all 0.25s;
	top: 0;
	display: grid;
	justify-content: center;
	align-items: center;
	pointer-events: none;
	opacity: 0;
}
 
.open {
	opacity: 1;
	pointer-events: all;
}
 
.container,
.delete_container {
    padding: 32px;
}

button {
    padding-inline-start: 16px;
    padding-inline-end: 16px;
}
.warning {
	font-size: 24px;
	color: white;
}

.confirm_delete {
	background-color: red;
	color: white;
}

.cancel_delete {
	background-color: green;
	color: white;
}